import React from "react";
import { NavBar } from "./navBar";

/* Page Zone */
import { Level } from "./level";
import { Inventory } from "./inventory.js";
import { Teaching } from "./teaching.js";
import { Shop } from "./shop.js";

/* Image Zone */
import menu from "./images/menu.png";
import gameImg from "./images/game.png";
import bagImg from "./images/bag.png";
import conceptImg from "./images/concept.png";
import homeImg from "./images/home.png";
import shopImg from "./images/shop.png";
import gemImg from "./images/gem.png";
import dogImg from "./images/dog.jpg";
import catImg from "./images/cat.jpg";

export class Screen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pagesAvail: [homeImg, gameImg, bagImg, conceptImg, shopImg],
      curPage: homeImg,
      dollar: 0,
      earn: 0,
      showNavBar: false,
      game: {
        text: "1+1",
        correctAnswer: "2",
        choices: ["1", "2", "3", "4", "5"],
        status: "",
        chosen: ["default"],
        score: 0,
        life: 3,
        numA: 1,
        numB: 1,
        multiplier: 0,
      },
      coinBag: {
        numA: 1,
        numB: 1,
        chosenCoinBag: [],
        showCoinBag: false,
      },
      shopItem: {
        dog: {
          price: 3,
          currency: [gemImg],
          img: [dogImg],
          locked: true,
        },
        cat: {
          price: 4,
          currency: [gemImg],
          img: [catImg],
          locked: true,
        },
      },
      warning: "",
    };
    this.handleNavClick = this.handleNavClick.bind(this);
    this.handleLevelClick = this.handleLevelClick.bind(this);
    this.handleMenuClick = this.handleMenuClick.bind(this);
    this.initializeGame = this.initializeGame.bind(this);
    this.handleQuestionClick = this.handleQuestionClick.bind(this);
    this.handleTutorialClick = this.handleTutorialClick.bind(this);
    this.handleShopClick = this.handleShopClick.bind(this);
  }

  handleNavClick(page) {
    if (this.state.game.life <= 0) {
      this.initializeGame();
    }
    this.setState({ curPage: page, showNavBar: false });
  }

  componentDidMount() {
    this.initializeGame();
  }

  initializeGame() {
    let numA = Math.floor(Math.random() * 10);
    let numB = Math.floor(Math.random() * 10);
    let gameCopy = this.state.game;

    this.setState({
      earn: this.state.earn + this.state.game.score,
    });

    gameCopy.text = numA.toString() + " \u00d7 " + numB.toString() + " = ? ";
    gameCopy.correctAnswer = (numA * numB).toString();
    gameCopy.choices = this.produceNoDuplicateList(numA, numB);
    gameCopy.life = 3;
    gameCopy.multiplier = 0;
    gameCopy.score = 0;
    gameCopy.numA = numA;
    gameCopy.numB = numB;
    gameCopy.status = "";
    gameCopy.chosen = ["default"];

    this.setState({
      game: gameCopy,
    });
  }

  handleCoinBagClick(id) {
    let coinBagCopy = this.state.coinBag;
    if (coinBagCopy.chosenCoinBag.some((element) => element === id)) {
      coinBagCopy.chosenCoinBag = coinBagCopy.chosenCoinBag.filter(
        (element) => element !== id
      );
    } else {
      coinBagCopy.chosenCoinBag.push(id);
    }

    this.setState({ coinBag: coinBagCopy });
  }

  handleLevelClick(answer) {
    let temp = this.state.game.chosen.slice();
    let numA = Math.floor(Math.random() * 10);
    let numB = Math.floor(Math.random() * 10 + 1);
    temp.push(answer);
    let gameCopy = this.state.game;
    gameCopy.chosen = temp;
    this.setState({ game: gameCopy });
    console.log(this.state.game.multiplier);
    if (answer === this.state.game.correctAnswer) {
      let gameCopy = this.state.game;
      let coinBagCopy = this.state.coinBag;
      let multiplier;
      gameCopy.text = numA.toString() + " \u00d7 " + numB.toString() + " = ? ";
      gameCopy.correctAnswer = (numA * numB).toString();
      gameCopy.choices = this.produceNoDuplicateList(numA, numB);
      gameCopy.status = "Correct";
      gameCopy.chosen = ["default"];
      if (this.state.game.score >= 30) {
        multiplier = 5;
      } else if (this.state.game.score >= 18) {
        multiplier = 4;
      } else if (this.state.game.score >= 9) {
        multiplier = 3;
      } else if (this.state.game.score >= 3) {
        multiplier = 2;
      } else {
        multiplier = 1;
      }
      gameCopy.multiplier = multiplier;
      gameCopy.score = this.state.game.score + multiplier;
      gameCopy.numA = numA;
      gameCopy.numB = numB;
      coinBagCopy.chosenCoinBag = [];
      coinBagCopy.showCoinBag = false;
      coinBagCopy.numA = numA;
      coinBagCopy.numB = numB;
      this.setState({
        game: gameCopy,
        coinBag: coinBagCopy,
      });
    } else {
      let gameCopy = this.state.game;
      gameCopy.life = this.state.game.life - 1;
      gameCopy.status = "Wrong";
      this.setState({ game: gameCopy });
    }
  }

  produceNoDuplicateList(numA, numB) {
    let place = Math.floor(Math.random() * 4);
    let nextCorrectAns = (numA * numB).toString();
    let temp = [];

    while (temp.length < this.state.game.choices.length) {
      let randomNumber = (Math.floor(Math.random() * 10) * numB).toString();
      if (
        !temp.some((element) => {
          return element === randomNumber;
        })
      ) {
        temp.push(randomNumber.toString());
      }
    }
    if (
      !temp.some((element) => {
        return element === nextCorrectAns;
      })
    ) {
      temp[place] = nextCorrectAns;
    }
    return temp;
  }

  handleMenuClick() {
    this.setState({
      showNavBar: this.state.showNavBar ? false : true,
    });
  }

  handleQuestionClick() {
    let gameCopy = this.state.game;
    gameCopy.showCoinBag = this.state.game.showCoinBag ? false : true;
    this.setState({ game: gameCopy });
  }

  handleRetryClick() {
    this.initializeGame();
  }

  handleTutorialClick(id) {
    let coinBagCopy = this.state.coinBag;
    if (id === 0) {
      coinBagCopy.numA =
        this.state.coinBag.numA >= 10 ? 0 : this.state.coinBag.numA + 1;
    } else {
      coinBagCopy.numB =
        this.state.coinBag.numB >= 10 ? 0 : this.state.coinBag.numB + 1;
    }

    this.setState({ coinBag: coinBagCopy });
  }

  handleShopClick(name) {
    if (this.state.shopItem[name].locked) {
      if (this.state.earn >= this.state.shopItem[name].price) {
        let shopItemCopy = this.state.shopItem;
        shopItemCopy[name].locked = false;
        this.setState({
          earn: this.state.earn - this.state.shopItem[name].price,
          shopItem: shopItemCopy,
          warning: "Purchase successful.",
        });
      } else {
        this.setState({
          warning: "Not enough gem!",
        });
      }
    }
  }

  render() {
    let display = <body></body>;
    let navBar;
    let displayPrice = [];

    for (const property in this.state.shopItem) {
      console.log(!this.state.shopItem[property].locked);
      if (!this.state.shopItem[property].locked) {
        displayPrice.push(
          <img
            className="App-Welcome-Price"
            src={this.state.shopItem[property].img[0]}
            alt="price"
          />
        );
      }
    }
    //console.log(this.state);
    switch (this.state.curPage) {
      case homeImg:
        display = (
          <body className="App-body">
            <div className="Greeting">Welcome, World!</div>
            <br></br>
            <NavBar
              pages={this.state.pagesAvail.slice(1, 5)}
              curPage={this.state.curPage}
              onClick={(page) => this.handleNavClick(page)}
            />
            <br></br>
            <div>{displayPrice}</div>
            
          </body>
        );
        break;
      case gameImg:
        display = (
          <body className="App-body">
            <Level
              game={this.state.game}
              coinBag={this.state.coinBag}
              onClick={(level) => this.handleLevelClick(level)}
              coinBagOnClick={(id) => this.handleCoinBagClick(id)}
              onQuestionClick={() => this.handleQuestionClick()}
              onRetryClick={() => this.handleRetryClick()}
            />
          </body>
        );
        break;
      case bagImg:
        display = (
          <body className="App-body">
            <Inventory
              earn={this.state.earn}
              onNavClick={() => this.handleNavClick(gameImg)}
            />
          </body>
        );
        break;
      case conceptImg:
        display = (
          <body className="App-body">
            <Teaching
              numA={this.state.coinBag.numA}
              numB={this.state.coinBag.numB}
              coinBagOnClick={(id) => this.handleCoinBagClick(id)}
              chosen={this.state.coinBag.chosenCoinBag}
              tutorialOnClick={(id) => this.handleTutorialClick(id)}
            />
          </body>
        );
        break;
      case shopImg:
        display = (
          <body className="App-body">
            <Shop
              earn={this.state.earn}
              item={this.state.shopItem}
              onClick={(amount) => this.handleShopClick(amount)}
              displayWarning={this.state.warn}
              warning={this.state.warning}
            />
          </body>
        );
        break;
      default:
        break;
    }
    if (this.state.showNavBar) {
      navBar = (
        <NavBar
          pages={this.state.pagesAvail}
          curPage={this.state.curPage}
          onClick={(page) => this.handleNavClick(page)}
        />
      );
    } else {
      navBar = (
        <button
          className="App-Menu-Button"
          onClick={() => this.handleMenuClick()}
        >
          <img className="App-Menu-Image" src={menu} alt={menu} />
        </button>
      );
    }

    return (
      <div className="App">
        <header className="App-header">{navBar}</header>
        {display}
      </div>
    );
  }
}
