import React from "react";
import coinBag from "./images/coinBag.png";
import coin from "./images/coin.png";

export class CoinBag extends React.Component {
  renderCoin(index) {
    let showBag = this.props.chosen.some((element) => {
      return element === index;
    });
    let idName = index.toString();

    return (
      <Button
        showBag={showBag}
        key={idName}
        numB={this.props.numB}
        onClick={() => this.props.onClick(index)}
      />
    );
  }
  render() {
    let AllCoinBag = [];
    for (let i = 0; i < this.props.numA; i++) {
      AllCoinBag.push(this.renderCoin(i));
    }
    if (this.props.numA === 0) {
      AllCoinBag.push(
        <button className="App-CoinBag-Button">
          no bag
        </button>
      );
    }

    return <div>{AllCoinBag}</div>;
  }
}
class Button extends React.Component {
  render() {
    let display = [];
    if (!this.props.showBag) {
      display.push(
        <button key={this.props.key} className="App-CoinBag-Button" onClick={this.props.onClick}>
          <img className="CoinBag" src={coinBag} alt="Money Bag" />
        </button>
      );
    } else {
      for (let i = 0; i < this.props.numB; i++) {
        display.push(
          <button key={i} className="App-Coin-Button" onClick={this.props.onClick}>
            <img className="Coin" src={coin} alt="coin" />
          </button>
        );
      }
      if (this.props.numB === 0) {
        display = (
          <button key={this.props.key} className="App-NoCoin-Button" onClick={this.props.onClick}>
            no coins
          </button>
        );
      }
      display = <div className="App-Coins-Div">{display}</div>;
    }
    return display;
  }
}
