import React from "react";
import { MultipleChoice } from "./multipleChoice";
import { CoinBag } from "./coinBag";
import gem from "./images/gem.png";
import heart from "./images/heart.png";
import retry from "./images/retry.png";

export class Level extends React.Component {
  render() {
    let allGem = [];
    let allHeart = [];
    let display = <div></div>;
    let coinBag = <div></div>;
    //console.log(this.props.game);
    for (let i = 0; i < this.props.game.score-this.props.game.multiplier; i++) {
      allGem.push(
        <button className="App-Gem-Button">
          <img className="App-GreenGem-Img" src={gem} alt="Gem" />
        </button>
      );
    }
    for (let i = 0; i < this.props.game.multiplier; i++) {
      allGem.push(
        <button className="App-Gem-Button">
          <img className="App-NewGem-Img" src={gem} alt="Gem" />
        </button>
      );
    }
    for (let i = 0; i < this.props.game.life; i++) {
      allHeart.push(
        <button className="App-Heart">
          <img className="heart" src={heart} alt="Heart" />
        </button>
      );
    }
    if (this.props.game.showCoinBag) {
      coinBag = (
        <CoinBag
          numA={this.props.game.numA}
          numB={this.props.game.numB}
          onClick={(index) => this.props.coinBagOnClick(index)}
          chosen={this.props.coinBag.chosenCoinBag}
        />
      );
    }

    if (this.props.game.life > 0) {
      display = (
        <div className="MultipleChoiceQuestion">
          <button
            className="App-Question-Button"
            onClick={this.props.onQuestionClick}
          >
            {this.props.game.text}
          </button>
          <MultipleChoice
            choices={this.props.game.choices}
            onClick={(i) => this.props.onClick(i)}
            chosen={this.props.game.chosen}
          />
          <div className="score">{allHeart}</div>
          <div className="score">{allGem}</div>
          {coinBag}
        </div>
      );
    } else {
      let gemDisplay = <div></div>;
      if (this.props.game.score > 0) {
        gemDisplay = (
          <div>
            <p>Your Loot</p>
            <div>{allGem}</div>
          </div>
        );
      } else {
        gemDisplay = (
          <div>
            <p>Nothing getted. Try again to mine more!!</p>
          </div>
        );
      }
      display = (
        <div className="MultipleChoiceQuestion">
          <button
            className="App-Retry-Button"
            onClick={this.props.onRetryClick}
          >
            <img className="App-Retry" src={retry} alt="Retry" />
          </button>
          <div>
            <button
              className="App-Question-Button"
              onClick={this.props.onQuestionClick}
            >
               {`${this.props.game.numA.toString()} \u00d7 ${this.props.game.numB.toString()} = ${this.props.game.correctAnswer}`}
            </button>
            {gemDisplay}
          </div>
        </div>
      );
    }

    return <div>{display}</div>;
  }
}
