import React from "react";

export class Item extends React.Component {
  renderLockedItem(item) {
    return <img className="App-Item-Locked" src={item} alt={item} />;
  }

  renderUnlockedItem(item) {
    return <img className="App-Item-Unlocked" src={item} alt={item} />;
  }

  renderCurrency(currency) {
    return (
      <div>
        <img className="App-Item-Currency" src={currency} alt={currency} />
        {"\u00d7" + this.props.price}
      </div>
    );
  }

  render() {
    let displayItem = <img alt="blank" />;
    let LockedItemImgs = this.props.src.map((page) =>
      this.renderLockedItem(page)
    );
    let UnlockedItemImgs = this.props.src.map((page) =>
      this.renderUnlockedItem(page)
    );
    let displayCurrency = <img alt="blank" />;
    if (this.props.locked) {
      displayItem = LockedItemImgs[0];
      displayCurrency = this.props.currency.map((page) =>
      this.renderCurrency(page))
    } else {
      displayItem = UnlockedItemImgs[0];
    }

    return (
      <div>
        {displayItem}
        {displayCurrency[0]}
      </div>
    );
  }
}
