import React from "react";
import { Item } from "./item";

export class Shop extends React.Component {
  renderItem(property) {
    return (
      <div className="App-Shop-Item">
        <Button
          src={this.props.item[property].img}
          currency={this.props.item[property].currency}
          price={this.props.item[property].price}
          onClick={() => this.props.onClick(property)}
          locked={this.props.item[property].locked}
        />
      </div>
    );
  }
  render() {
    let display = [];
    for (const property in this.props.item) {
      display.push(this.renderItem(property));
    }
    return (
      <div>
        {this.props.warning}
        {display}
      </div>
    );
  }
}

class Button extends React.Component {
  render() {

    return (
      <button
        key={this.props.src}
        className="App-JustHover-Button"
        onClick={this.props.onClick}
      >
        <Item
          locked={this.props.locked}
          src={this.props.src}
          price={this.props.price}
          currency={this.props.currency}
        />
      </button>
    );
  }
}
