import React from "react";

export class MultipleChoice extends React.Component {
  renderAnswer(answer,index) {
    let status = this.props.chosen.some(element=>{return element === answer});
    let idName = 'button'+ index.toString();
    return (
      <Button
        key={idName}
        answer={answer}
        onClick={() => this.props.onClick(answer)}
        disabled={status}
      >
        {answer}
      </Button>
    );
  }
  componentWillUnmount() {
  }
  render() {
    let buttons = this.props.choices.map((choice,index) => this.renderAnswer(choice,index));

    return <div>{buttons}</div>;
  }
}

class Button extends React.Component {
  render() {
    return (
      <button
        key={this.props.key}
        choice={this.props.answer}
        onClick={this.props.onClick}
        className="App-Choice-button"
        disabled={this.props.disabled}
      >
        {this.props.answer}
      </button>
    );
  }
}
