import React from "react";
import gem from "./images/gem.png";
import gameImg from "./images/game.png";

export class Inventory extends React.Component {
  render() {
    let allGem = [];
    for (let i = 0; i < this.props.earn; i++) {
      allGem.push(
        <button className="App-GemInventory-Button">
          <img className="App-Gem-Img" src={gem} alt="Gem" />
        </button>
      );
      if((i+1)%10===0){
        allGem.push(<div></div>)
      }
    }
    if (this.props.earn === 0) {
      allGem = (
        <p>
          Press{" "}
          <button
            key={this.props.key}
            onClick={this.props.onNavClick}
            className="App-Nav-button"
          >
            <img
              className="App-Nav-Img"
              src={gameImg}
              alt={gameImg}
            />
          </button>{" "}
          and get gems!
        </p>
      );
    }

    return <div>{allGem}</div>;
  }
}
