import React from "react";
import { CoinBag } from "./coinBag";

export class Teaching extends React.Component {
  renderCoin(index) {
    //console.log(index)
    let label = index === 0 ? this.props.numA : this.props.numB;
    if (index === 0) {
      return (
        <Button
          className="App-Question-Button"
          numA={label}
          onClick={() => this.props.tutorialOnClick(index)}
          key={index}
        />
      );
    } else {
      return (
        <Button
          className="App-Question-Button"
          numA={label}
          onClick={() => this.props.tutorialOnClick(index)}
          key={index}
        />
      );
    }
  }
  render() {
    let allGem = [];
    for (let i = 0; i < 2; i++) {
      allGem.push(this.renderCoin(i));
    }
    return (
      <div>
        <div>
          {allGem[0]}
          <button className="App-Question-Button">{"\u00d7"}</button>
          {allGem[1]}
          <button className="App-Question-Button">{" = "+(this.props.numA*this.props.numB).toString()}</button>
        </div>
        <CoinBag
          numA={this.props.numA}
          numB={this.props.numB}
          onClick={(index) => this.props.coinBagOnClick(index)}
          chosen={this.props.chosen}
        />
      </div>
    );
  }
}
class Button extends React.Component {
  render() {
    return (
      <button
        key={this.props.key}
        className="App-Question-Button"
        onClick={this.props.onClick}
      >
        {this.props.numA}
      </button>
    );
  }
}
