import React from "react";

export class NavBar extends React.Component {
  renderPage(page) {
    return (
      <Button buttonKey={page} page={page} onClick={() => this.props.onClick(page)}>
        {page}
      </Button>
    );
  }
  render() {
    let buttons = this.props.pages.map((page) => this.renderPage(page));

    return <nav>{buttons}</nav>;
  }
}

class Button extends React.Component {
  render() {
    return (
      <button
        key={this.props.buttonKey}
        choice={this.props.page}
        onClick={this.props.onClick}
        className="App-Nav-button"
      >
        <img
          className="App-Nav-Img"
          src={this.props.page}
          alt={this.props.page}
        />
      </button>
    );
  }
}
